.card-collection-container {
    padding: 0 1.09375rem;
    outline: 0 !important;
    display: block;
}

.card-collection {
    position: relative;
    cursor: pointer;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: -webkit-transform .3s cubic-bezier(0, .25, .1, 1);
    transition: -webkit-transform .3s cubic-bezier(0, .25, .1, 1);
    transition: transform .3s cubic-bezier(0, .25, .1, 1);
    transition: transform .3s cubic-bezier(0, .25, .1, 1), -webkit-transform .3s cubic-bezier(0, .25, .1, 1);
    overflow: hidden;
}

.card-collection:hover {
    -webkit-transform: scale(1.075);
            transform: scale(1.075);
}

.card-collection .overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0, 0, 0, .5)));
    background: linear-gradient(transparent, rgba(0, 0, 0, .5));
}

.card-collection .content {
    position: absolute;
    /*bottom: 0; have to get this working to make it go bottom aligned*/
    top: 200px;
    width: 100%;
    display: block;
    padding: 2rem;
    text-shadow: 0 0 0.8em rgba(0, 0, 0, .8);
}

.card-collection .heading {
    color: #FFF;
    display: block;
    margin-bottom: .125rem;
    font-weight: 600;
    font-size: 1.25rem;
}

.card-collection .subheading {
    font-weight: 400;
    font-size: 1rem;
    color: #FFF;
    display: block;
    margin-bottom: 1rem;
}

.card-collection .photo-link {
    color: #FFF;
    text-decoration: none;
    position: relative;
    -webkit-transition: color 150ms ease;
    transition: color 150ms ease;
}

.card-collection:hover .photo-link:after {
    opacity: 0;
}

.card-collection .photo-link:after {
    display: block;
    height: 2px;
    position: absolute;
    background-color: #FFF;
    bottom: .0625rem;
    opacity: .5;
    -webkit-transition: opacity 150ms ease;
    transition: opacity 150ms ease;
    width: 100%;
    left: 0;
    content: '';
}

